.friends__container{
    max-height: calc(100svh - 149px);
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    margin:20px auto 0;
    padding: 0 16px;
    position: relative;
}

.mt_40{
    margin-top: 50svh;
}

.mt_30{
    margin-top: 40svh;
}
.mt_20{
    margin-top: 10svh;
}

.friends__container:before{
    content: "";
    background-image: url("../components/images/background.png");
    position: absolute;
    width: 100svw;
    left: 0;
    height: 100svh;
    top: -16px;
    z-index: -1;
    object-fit: cover;
    background-position: center; 
    background-repeat: no-repeat; 
}
.friends__container__title{
    text-align: center;
    font-size: 32px;
    font-family: Poppins_SemiBold;
}
.friends__container__description{
    text-align: center;
    font-size: 16px;
    margin: 0 auto;
}
.airdrop__img{
    display: flex;
    margin: 64px auto 90px;
    position: relative;
    user-select: none;
}
.airdrop__img:before{
    content: "";
    position: absolute;
    width: 93px;
    height: 93px;
    z-index: 10;
    background: #FEB92F;
    border-radius: 50px;
    background-image: url("../components/images/airdropIcon.png");
    background-repeat: no-repeat;
    background-size: 93px 78px;
    background-position: center 10px;
    margin: 0 auto;
    left: 0;
    right: 0;
    bottom: 7%;
    text-align: center;
    user-select: none;
    box-shadow: 0 -40px 200px 35px #FEB92F;
}




.button_full_width {
    width: 100%;
}

.ton_connect_button {
    width: 100% !important;
    box-shadow: none !important; 
    border: none; 
    outline: none;
}

.ton_connect_button:hover,
.ton_connect_button:focus,
.ton_connect_button:active {
    box-shadow: none !important; 
}

.mb_20 {
    margin-bottom: 20svh;
}

.mb_40 {
    margin-bottom: 40svh;
}

.dflex_justifycenter {
    display: flex;
    justify-content: center;
}

.friends_ton_connect_button__9tGG9 button {
    box-shadow: none !important;
}

.friends_ton_connect_button__9tGG9 button:hover,
.friends_ton_connect_button__9tGG9 button:focus,
.friends_ton_connect_button__9tGG9 button:active {
    box-shadow: none !important;
}
